
import CONFIGS from '../config'
import { Message } from 'element-ui'
import cookies from '@/utils/cookie'

function checkStatus( status ) {
  let errMessage = ''
  switch ( status ) {
    case 400:
      errMessage = ''
      break
    case 401:
      errMessage = ''
      break
    case 403:
      errMessage = ''
      break
    case 404:
      errMessage = ','
      break
    case 405:
      errMessage = ''
      break
    case 408:
      errMessage = ''
      break
    case 500:
      errMessage = ''
      break
    case 501:
      errMessage = ''
      break
    case 502:
      errMessage = ''
      break
    case 503:
      errMessage = ''
      break
    case 504:
      errMessage = ''
      break
    case 505:
      errMessage = 'http'
      break
    default:
      errMessage = ``
  }
  return errMessage
}

function getParams( payload ) {
  const { method, data } = payload
  if ( ['post', 'put', 'patch', 'delete'].indexOf( method ) >= 0 ) {
    payload.data = data
  } else {
    payload.params = data
    delete payload.data
  }
  return payload
}

const token = cookies.get( CONFIGS.COOKIE_PREFIX + 'token' )

export default function( payload ) {
  const { app, $axios, store } = payload

  $axios.defaults.timeout = CONFIGS.TIMEOUT
  $axios.defaults.withCredentials = false
  $axios.defaults.headers.common['token'] = token || ''
  $axios.defaults.headers['Content-Type'] = 'application/json'
  $axios.defaults.baseURL = '/v1'

  // const isServer = !!process.server
  const isClient = !!process.client

  // 
  $axios.interceptors.request.use( config => {
    if ( isClient && !navigator.onLine ) {
      Message.error( {
        message : '',
        type : 'error',
        duration : CONFIGS.DURATION
      } )
      return Promise.reject( '' )
    }
    // config.data = qs.stringify(config.data)

    return getParams( config )
  }, ( error ) => {
    return Promise.reject( error )
  } )

  // 
  $axios.interceptors.response.use( response => {
    const result = response.data
    const type = Object.prototype.toString.call( result )
    //  
    if ( type === '[object Blob]' || type === '[object ArrayBuffer]' ) {
      return result
    } else {
      const { code, message } = result
      const isErrorToken = CONFIGS.LOGIN_ERROR_CODE.find( item => item.code == code )
      const isWhiteCode = CONFIGS.WHITE_CODE_LIST.find( item => item.code == code )

      if ( isErrorToken ) {
        store.dispatch( 'user/LoginOut' )
        app.router.push( '/login' )
        isClient && window.location.reload()
      } else if ( !isWhiteCode ) {
        Message( {
          message : message || 'Error',
          type : 'error',
          duration : CONFIGS.DURATION
        } )
        return Promise.reject( message || 'Error' )
      } else {
        return result
      }
    }
  }, error => {
    if ( error && error.response ) {
      error.message = checkStatus( error.response.status )
    }
    Message( {
      message : error.message || '',
      type : 'error',
      duration : CONFIGS.DURATION
    } )
    return Promise.reject( error.message )
  } )
}

